import React, { useState } from "react";
import { TodoIcon } from "./";

function CompleteIcon({ completed, onToggle }) {
  const [isHovered, setIsHovered] = useState(false); // Estado para manejar hover

  // Determinar color del √≠cono din√°micamente
  const iconColor = completed
    ? isHovered
      ? "red" // Rojo si est√° completado y hay hover
      : "green" // Verde si est√° completado pero no hay hover
    : isHovered
    ? "green" // Verde si NO est√° completado pero hay hover
    : "gray"; // Gris si NO est√° completado y no hay hover
  return (
    <TodoIcon
      type="check"
      color={iconColor} // Color din√°mico
      onClick={onToggle} // Alterna el estado al hacer clic
      onMouseEnter={() => setIsHovered(true)} // Activar hover
      onMouseLeave={() => setIsHovered(false)} // Desactivar hover
    />
  );
}

export { CompleteIcon };
